/**=====================
     about css start
==========================**/
.about-section {
  .about-wrap {
    .about-content {
      align-items: center;
      &.row {
        margin: 0;
        > div {
          padding: 0;
        }
      }
      .about-image {
        position: relative;
        overflow: hidden;
        border-radius: 10px;
        > img {
          box-shadow: 6.691px 7.431px 12.5px rgba(0, 128, 255, 0.07);
          width: 100%;
        }
        .about-overlay {
          background-image: var(--theme-gradient4);
          position: absolute;
          height: 100%;
          width: 100%;
          left: 0;
          top: 0;
          bottom: 0;
          right: 0;
          opacity: 0;
          transition: all 0.4s ease-in-out 0s;
        }
        .overlay-content {
          position: absolute;
          text-align: center;
          padding-left: 1em;
          padding-right: 1em;
          width: 100%;
          top: 10%;
          left: 50%;
          opacity: 0;
          transform: translate(-50%, -50%);
          transition: all 0.3s ease-in-out 0s;
          ul {
            margin-bottom: 15px;
            li {
              a {
                width: 40px;
                height: 40px;
                border-radius: 20px;
                background-color: $white;
                @extend %flex-center;
              }
              + li {
                margin-left: 10px;
              }
            }
          }
          span {
            letter-spacing: 14px;
            color: $white;
            font-weight: 700;
            position: relative;
            text-transform: uppercase;
            &::before,
            &::after {
              position: absolute;
              content: "";
              background-color: $white;
              width: 16px;
              height: 1px;
              top: 9px;
            }
            &::before {
              left: -35px;
            }
            &::after {
              right: -22px;
            }
          }
        }
      }
      &:hover {
        .about-overlay {
          opacity: 0.7;
        }
        .overlay-content {
          top: 50%;
          opacity: 1;
          ul {
            li {
              &:nth-child(2) {
                a {
                  animation: fadeInDown 900ms ease-in-out;
                }
              }
              &:nth-child(3) {
                a {
                  animation: fadeInDown 1100ms ease-in-out;
                }
              }
              &:nth-child(4) {
                a {
                  animation: fadeInDown 1300ms ease-in-out;
                }
              }
            }
          }
        }
      }
      .our-details {
        padding: 40px;
        border-radius: 0 10px 10px 0;
        box-shadow: 3.346px 3.716px 25px rgba($black, 0.07);
        background-color: $white;
        position: relative;
        span {
          font-size: 14px;
          margin-bottom: -2px;
          svg {
            width: 14px;
            height: 15px;
            vertical-align: -2px;
          }
        }
        a.btn {
          padding: 8px 26px;
          svg {
            width: 16px;
            height: 20px;
            margin-right: 7px;
            vertical-align: -5px;
          }
        }
        &::after {
          position: absolute;
          content: "";
          width: 5px;
          height: 55px;
          border-radius: 2px;
          background-color: var(--theme-deafult2);
          right: 0;
          top: 120px;
        }
        .d-flex {
          align-items: center;
        }
        h6 {
          color: var(--theme-deafult);
          font-weight: 700;
        }
        h3 {
          line-height: 28px;
          font-weight: 700;
          font-size: calc(16px + (20 - 16) * ((100vw - 320px) / (1920 - 320)));
        }
        p {
          line-height: 1.8;
          margin: 8px 0;
          font-size: 13px;
          color: #647589;
        }
      }
    }
    &.about-2 {
      .about-content {
        .about-image {
          border-radius: 0;
          .about-overlay {
            background: rgba($title-color, 0.7);
          }
        }
        .our-details {
          margin: -70px auto 0;
          border-radius: 0;
          width: 80%;
          &::after {
            background-color: var(--theme-deafult5);
            height: 0;
            opacity: 0;
          }
          h6 {
            color: var(--theme-deafult5);
          }
          p {
            color: rgba($title-color, 0.7);
          }
        }
        &:hover {
          .our-details {
            &::after {
              height: 55px;
              transition: height 0.3s;
              opacity: 1;
            }
          }
        }
      }
      .slick-list {
        margin-bottom: -30px;
        .slick-track {
          .slick-slide {
            margin-bottom: 0;
            > div {
              margin-bottom: 30px;
            }
          }
        }
      }
    }
  }
  &.about-inner {
    .about-wrap {
      .about-content {
        .our-details {
          &::after {
            background-color: var(--theme-deafult3);
          }
          h6 {
            color: var(--theme-deafult3);
          }
        }
      }
      &.about-1 {
        .about-content {
          .about-image {
            .about-overlay {
              background-image: var(--theme-gradient7);
            }
          }
        }
      }
    }
  }
  &.about-color6 {
    .about-wrap {
      .about-content {
        .about-image {
          .about-overlay {
            background-image: var(--theme-gradient13);
          }
        }
        .our-details {
          h6 {
            color: var(--theme-deafult9);
          }
          &::after {
            background-color: var(--theme-deafult9);
          }
        }
      }
    }
  }
  &.color-7 {
    .about-wrap {
      .about-content {
        .our-details {
          &::after {
            background-color: var(--theme-deafult10);
          }
          h6 {
            color: var(--theme-deafult10);
          }
        }
      }
    }
  }
}
.about-box {
  .agent-image {
    border-radius: 5px;
    overflow: hidden;
    box-shadow: 0 0 35px rgba($black, 0.07);
    position: relative;
    .overlay-agent {
      position: absolute;
      transition: all 0.4s ease-in-out;
      top: 0;
      left: 0;
      width: 100%;
      height: 100%;
      opacity: 0;
      .agent-details {
        opacity: 0;
        transform: scale(1.2);
        transition: 0.4s;
        background-color: $white;
        height: 85%;
        width: 85%;
        position: absolute;
        top: 8%;
        left: 8%;
        padding: 40px;
        z-index: 1;
        ul {
          margin-top: 15px;
          li {
            a {
              width: 36px;
              height: 36px;
              border-radius: 100%;
              box-shadow: 0 0 20px 0 rgba($theme-font-color, 0.1);
              @extend %flex-center;
            }
            + li {
              margin-left: 10px;
            }
          }
        }
        h5 {
          margin-bottom: 0;
          line-height: 28px;
          color: $title-color;
          transition: 0.4s;
          font-weight: 500;
        }
        h6 {
          color: var(--theme-deafult7);
          font-weight: 700;
          align-items: center;
          transition: 0.4s;
        }
        p {
          margin: 15px 0;
          font-size: 12px;
          line-height: 23px;
          transition: 0.4s;
          color: rgba($title-color, 0.7);
        }
        > span {
          color: $title-color;
          transition: 0.4s;
        }
      }
      &::after {
        @extend %common-lightafter;
        opacity: 0.7;
        background-image: var(--theme-gradient9);
      }
    }
  }
  &:hover {
    .agent-image {
      .overlay-agent {
        opacity: 1;
        .agent-details {
          opacity: 1;
          transform: scale(1);
          transition: 0.4s;
          h5,
          h6,
          p,
          > span {
            transition: 0.4s;
          }
        }
      }
    }
  }
}
/**=====================
     about css end
==========================**/
