@keyframes effect {
  0% {
    transform: scale(1);
  }
  75% {
    transform: scale(1.4);
    opacity: 0.4;
  }
  100% {
    transform: scale(1.6);
    opacity: 0;
  }
}
@keyframes sonarEffect {
  0% {
    opacity: 0.05;
  }
  40% {
    opacity: 0.08;
    box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.1), 0 0 10px 10px var(--theme-deafult3), 0 0 0 10px rgba(255, 255, 255, 0.5);
  }
  100% {
    box-shadow: 0 0 0 2px rgba(255, 255, 255, 0.1), 0 0 10px 10px var(--theme-deafult3), 0 0 0 10px rgba(255, 255, 255, 0.5);
    transform: scale(1.3);
    opacity: 0;
  }
}
@keyframes spin {
  100% {
    transform: rotate(1turn);
  }
}
@keyframes bubble {
  100% {
    transform: scale(1);
  }
}
@keyframes heartbit {
  0% {
    transform: scale(0);
    opacity: 0;
  }
  25% {
    transform: scale(0.1);
    opacity: 0.1;
  }
  50% {
    transform: scale(0.5);
    opacity: 0.3;
  }
  75% {
    transform: scale(0.8);
    opacity: 0.5;
  }
  100% {
    transform: scale(1);
    opacity: 0;
  }
}
@keyframes search-bg-1 {
  0% {
    right: -20px;
  }
  50% {
    right: -15px;
  }
  100% {
    right: -11px;
  }
}
@keyframes search-bg-2 {
  0% {
    right: -10px;
  }
  50% {
    right: -5px;
  }
  100% {
    right: 0;
  }
}
@keyframes search-bg-3 {
  0% {
    right: 0;
  }
  50% {
    right: 5px;
  }
  100% {
    right: 10px;
  }
}
@keyframes animation-x {
  0% {
    background-position-x: 0;
  }
  100% {
    background-position-x: 1920px;
  }
}
@keyframes bounce-1 {
  0% {
    transform: translateY(0);
  }
  50% {
    transform: translateY(-7px);
  }
  100% {
    transform: translateY(0);
  }
}
@keyframes smoothScroll {
  0% {
    -webkit-transform: translateY(-40px);
    transform: translateY(-40px);
  }
  100% {
    -webkit-transform: translateY(0px);
    transform: translateY(0px);
  }
}
@keyframes sway {
  0% {
    transform: rotate(8deg);
  }
  50% {
    transform: rotate(-8deg);
  }
  100% {
    transform: rotate(8deg);
  }
}
